<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container-tab">
      <van-tabs v-model="active" swipeable animated sticky class="tabs-sticky" @change="change()">
        <van-tab v-for="(item,index3) of tabs" :key="index3" :title="item">
          <van-list
            v-if="pageStyles===0"
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            :immediate-check="false"
            @load="onLoad"
          >
            <div v-for="(order,index) of orderList" :key="index" class="order-item van-clearfix">
              <div class="order-loop">
                <div class="order-tit">
                  <div class="left">{{ order.gysmc }}</div>
                  <div class="right"><font class="red">{{ order.statusValue }}</font></div>
                </div>
                <div v-for="(spxx,indexs) of order.detailList" :key="indexs">
                  <div class="order-con" :style="order.detailList.length>1?'margin-top:20px':''">
                    <div class="goods-img" @click="imgPreview(spxx.img?spxx.img:defaultImg)"><img :src="spxx.img" :onerror="defaultImg"></div>
                    <div class="goods-name">
                      <div class="name" @click="toOrderSnapshot(spxx.id)">{{ spxx.product_name }}</div>
                      <van-tag plain type="primary">{{ spxx.sourceName }}</van-tag>
                    </div>
                    <div class="goods-price">
                      <div class="jiage">¥{{ spxx.price }}</div>
                      <div class="shuliang">× {{ spxx.total }}</div>
                      <div class="pingjia">
                        <van-button v-if="order.status>=35 && order.status!==55 && spxx.evaluation_status!==2" plain size="small" @click="goorderevaluation(spxx.id)">评价</van-button>
                        <div v-if="spxx.evaluation_status===2" class="btn">已评价</div>
                      </div>
                    </div>
                  </div>
                  <div v-if="spxx.accessorList.length>0 && spxx.check" class="common-more" @click="showMore1(index,indexs)">
                    <div class="more">
                      <em>隐藏</em>
                    </div>
                  </div>
                  <div v-if="spxx.accessorList.length>0 && !spxx.check" class="common-more" @click="showMore1(index,indexs)">
                    <div class="more">
                      <em>显示</em>
                    </div>
                  </div>
                  <div v-if="spxx.check" class="accessories">
                    <div v-for="(item1,index1) of spxx.accessorList" :key="index1" class="accessories-list">
                      <div class="accessories-tit">{{ item1.name }}</div>
                      <div class="accessories-info">¥{{ item1.price }}</div>
                    </div>
                  </div>
                </div>
                <div class="order-foot">
                  <van-button plain size="small" @click="goorderdetail(order.id)">订单详情</van-button>
                  <van-button v-if="order.status >= 15 && order.status < 35" plain size="small" @click="cancleOrder(order.id)">撤销订单</van-button>
                  <van-button v-if="order.status === 30" plain size="small" @click="goodsQs(order.id)">签收</van-button>
                </div>
              </div>
            </div>
          </van-list>
          <div v-if="pageStyles===1">
            <error-page
              :page-style="pageStyles"
              @errorButton="parentReceive"
            />
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <Backtop class="scrollTop-bar" />
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import errorPage from '@/components/result-page/index'
import { pageRouter } from '@/common/js/mixin.js'
import { ImagePreview } from 'vant'
import { myOrder, cancleOrder } from '@/store/module.js'
import Backtop from '@/components/backtop/backtop.vue' // 返回顶部
export default {
  name: 'MyOrder',
  components: {
    headerBar,
    errorPage,
    Backtop
  },
  mixins: [pageRouter],
  data() {
    return {
      state: true,
      tabs: ['全部', '待发货', '待收货', '已签收'],
      loading: true,
      finished: false,
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"',
      check: false,
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      pageStyles: 0,
      SearchInput: {
        ddbh: '', // 订单编号
        ddzt: '', // 订单状态
        sfddh: '', // 三方订单号
        fkzt: '', // 付款状态
        kssj: ['', '']// 时间
      },
      active: 0,
      checked: true,
      orderList: [],
      qbList: [],
      dfhList: [],
      dshList: [],
      ywcList: [],
      headerData: {
        title: '我的订单',
        leftUrl: 'back',
        leftText: ' '
      },
      currentPage: 1,
      totalPages: 0
    }
  },
  created() {
    const that = this
    this.$bus.$on('pjSearch', () => {
      that.change()
    })
    if (this.$route.params && this.$route.params.cs) {
      this.active = this.$route.params.cs
    }
    // this.change()
    this.init()
  },
  methods: {
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    goodsQs(orderid) {
      this.$store
        .dispatch('member/signOrderAction', { e_order_id: orderid })
        .then(res => {
          this.$toast.success('签收成功')
          this.change()
        })
        .catch(() => {
          this.loading = false
        })
    },
    showMore1(index, detailIndex) {
      this.orderList[index].detailList[detailIndex].check = !this.orderList[index].detailList[detailIndex].check
      this.orderList[index].detailList.splice(detailIndex, 1, this.orderList[index].detailList[detailIndex])
    },
    showMore2(index, detailIndex) {
      this.dfhList[index].detailList[detailIndex].check = !this.dfhList[index].detailList[detailIndex].check
      this.dfhList[index].detailList.splice(detailIndex, 1, this.dfhList[index].detailList[detailIndex])
    },
    showMore3(index, detailIndex) {
      this.dshList[index].detailList[detailIndex].check = !this.dshList[index].detailList[detailIndex].check
      this.dshList[index].detailList.splice(detailIndex, 1, this.dshList[index].detailList[detailIndex])
    },
    showMore4(index, detailIndex) {
      this.ywcList[index].detailList[detailIndex].check = !this.ywcList[index].detailList[detailIndex].check
      this.ywcList[index].detailList.splice(detailIndex, 1, this.ywcList[index].detailList[detailIndex])
    },
    getPrizeLog(data) {
      if (this.active === 0) {
        this.qbList = data || []
        for (let i = 0; i < this.qbList.length; i++) {
          for (let j = 0; j < this.qbList[i].detailList.length; j++) {
            this.qbList[i].detailList[j].check = false
          }
        }
      }
      if (this.active === 1) {
        this.dfhList = data || []
        for (let i = 0; i < this.dfhList.length; i++) {
          for (let j = 0; j < this.dfhList[i].detailList.length; j++) {
            this.dfhList[i].detailList[j].check = false
          }
        }
      }
      if (this.active === 2) {
        this.dshList = data || []
        for (let i = 0; i < this.dshList.length; i++) {
          for (let j = 0; j < this.dshList[i].detailList.length; j++) {
            this.dshList[i].detailList[j].check = false
          }
        }
      }
      if (this.active === 3) {
        this.ywcList = data || []
        for (let i = 0; i < this.ywcList.length; i++) {
          for (let j = 0; j < this.ywcList[i].detailList.length; j++) {
            this.ywcList[i].detailList[j].check = false
          }
        }
      }
    },
    // 跳转交易快照
    toOrderSnapshot(id) {
      this.$store.commit('goods/SET_SPID', id)
      this.$router.push({
        name: 'OrderSnapshot'
      })
    },
    parentReceive() {
      this.change()
    },
    init() {
      if (this.state) {
        this.state = false
      } else {
        return
      }
      let zt = ''
      if (this.active === 0) {
        zt = ''
      }
      if (this.active === 1) {
        zt = 'dfh'
      }
      if (this.active === 2) {
        zt = 'dsh'
      }
      if (this.active === 3) {
        zt = 'dpj'
      }
      const data = {
        pageno: this.currentPage,
        pagerow: 10,
        order_status_query: this.SearchInput.ddzt,
        order_ddbh_query: this.SearchInput.ddbh,
        order_pay_state_query: this.SearchInput.fkzt,
        third_order_id_query: this.SearchInput.sfddh,
        kssj_query: this.SearchInput.kssj[0].replace('-', '').replace('-', '').replace(' ', '').replace(':', '').replace(':', ''),
        jssj_query: this.SearchInput.kssj[1].replace('-', '').replace('-', '').replace(' ', '').replace(':', '').replace(':', ''),
        query: zt
      }
      myOrder.data = data
      this.asyncApiModule(myOrder).then(res => {
        this.loading = false
        this.totalPages = res.totalPages
        this.currentPage++
        this.state = true
        if (res.list) {
          this.pageStyles = 0
          for (let i = 0; i < res.list.length; i++) {
            res.list[i].check = false
            this.orderList.push(res.list[i])
          }
          if (this.currentPage > res.totalPages) {
            this.finished = true
          } else {
            this.finished = false
          }
        } else {
          this.pageStyles = 1
          this.finished = true
        }
      })
    },
    onLoad() {
      this.finished = false
      this.init()
    },
    change() {
      this.pageStyles = 0
      this.orderList = []
      this.loading = true
      this.finished = false
      this.currentPage = 1
      this.init()
    },
    // 撤销订单
    cancleOrder(orderid) {
      this.$dialog.confirm({
        message: '确认撤销订单么?'
      }).then(_ => {
        cancleOrder.data = { e_order_id: orderid }
        this.asyncApiModule(cancleOrder).then(res => {
          this.$toast.success('撤销成功')
          this.change()
          // this.goDefault(this.type)
        })
          .catch(() => {
            this.loading = false
          })
      })
        .catch(_ => {})
    },
    goBack() {
      this.$router.back()
    },
    goAddressadd() {
      this.$router.push({
        name: 'AddressAdd'
      })
    },
    goorderdetail(order_id) {
      this.$store.commit('member/SET_ORDERID', order_id)
      this.$router.push({
        name: 'OrderDetail'
      })
    },
    goorderevaluation(order_id) {
      this.$store.commit('member/SET_ORDERID', order_id)
      this.$router.push({
        name: 'OrderEvaluation'
      })
    },
    goorderexpress(order_id) {
      this.$store.commit('member/SET_ORDERID', order_id)
      this.$router.push('OrderExpress')
    }
  }
}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
